  font-family: "AmaticSC";
  src: url("../fonts/AmaticSC-Bold.eot");
  src: url("../fonts/AmaticSC-Bold.woff") format("woff"), url("../fonts/AmaticSC-Bold.ttf") format("truetype"), url("../fonts/AmaticSC-Bold.svg") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116; /*кирилица*/
  /*unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; /*латиница*/
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.eot");
  src: url("../fonts/Inter-Regular.woff") format("woff"), url("../fonts/Inter-Regular.ttf") format("truetype"), url("../fonts/Inter-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116; /*кирилица*/
  /*unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; /*латиница*/
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Inter", "Candara", sans-serif;
  font-style: normal;
  font-size: 1rem;
  line-height: 1.25;
  font-weight: normal;
  text-transform: unset;
  color: #000;
  background-color: #ffffff;
}

main {
  flex-grow: 1;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  text-decoration: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

.list-default {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container-fluid {
  width: 100%;
}

@media (max-width: 599.9px) {
  .container-fluid {
    padding: 0 15px;
  }
}
@media (min-width: 600px) and (max-width: 767.9px) {
  .container-fluid {
    padding: 0 20px;
  }
}
@media (min-width: 768px) and (max-width: 1169.9px) {
  .container-fluid {
    padding: 0 30px;
  }
}
@media (min-width: 1170px) and (max-width: 1439.9px) {
  .container-fluid {
    padding: 0 130px;
  }
}
@media (min-width: 1440px) {
  .container-fluid {
    padding: 0 150px;
  }
}
.hidden {
  display: none;
}

.hidden__no {
  display: inline-block;
}

@media (max-width: 767.9px) {
  .hidden_mobile {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 1169.9px) {
  .hidden_tablet {
    display: none;
  }
}
@media (min-width: 1170px) {
  .hidden_desktop {
    display: none;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%);
}